@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  background-color: #193d35;
  color: #ffffff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #ffffff;
}

.coin-menu {
  width: 900px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 10px;
  margin-right: 50px;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-input {
  width: 300px;
  height: 50px;
  padding-left: 20px;
  border-radius: 20px;
  border: none;
  outline: none;
  border: 1px solid #49ba92;
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

.info-n-cur {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.coin-all {
  border: 1px solid #49ba92;
  padding: 0 20px;
  border-radius: 20px;
  margin-bottom: 20px;
}

.coin-container {
  display: flex;
  justify-content: center;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 80px;
  width: 900px;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}

.coin-names {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 400px;
}

.coin h1 {
  font-size: 16px;
  width: 150px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin-symbol {
  text-transform: uppercase;
}

.coin-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.coin-price {
  width: 100px;
}

.coin-volume {
  width: 155px;
}

.coin-marketcap {
  width: 230px;
}

.coin-percent {
  width: 100px;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}

.converter {
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.converter h1 {
  font-size: 16px;
  width: 150px;
  margin-bottom: 8px;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.cur-row {
  display: flex;
  flex-direction: row;
}

.cur-input,
.symbol {
  border: 1px solid #49ba92;
  padding: 10px 20px;
  border-radius: 15px;
}

.cur-input {
  margin-right: 5px;
  outline: none;
}

.eq {
  font-weight: bold;
  font-size: 25px;
}

.my-cab-all {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.my-cab {
  width: 300px;
}
.my-cab h1 {
  font-size: 16px;
  width: 150px;
  margin-bottom: 8px;
}

.my-cab .cur-row {
  margin-bottom: 10px;
}

